import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {GetUserFeePackageList,GetUserFeePackageBuyList,ActivateUserFeePackage,InitialPayUserFeePackage,PayUserFeePackage} from 'api/user'
export default {
	components:{
		UniLoadMore
	},
	data() {
		return {
			tabs:['我的营销卡','购买营销卡','开通营销卡'],
			index:0,
			list:[],
			page:1,
			pageCount:0,
			moreStatus:"",
			cardId:'',
			cardPwd:'',
			
			userInfo:{},
			state:{}
		}
	},
	
	onPullDownRefresh() {
		if(this.index == 0){
			this.getList()
		}else if(this.index == 1){
			this.getAllList()
		}	 
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			if(this.index == 0){
				this.getList(this.page)
			}else if(this.index == 1){
				this.getAllList(this.page)
			}
			
		}	
	},
	onLoad() {
		this.$store.ready(state=>{
			this.state = state
			this.userInfo = state.userInfo
		
		})
	},
	onShow() {
		if(this.index == 0){
			this.getList()
		}else if(this.index == 1){
			this.getAllList()
		}
		
	},
	methods: {
		openDetail(item){
			uni.navigateTo({
				url:'/pages/user/card/detail?type='+this.index+'&id='+item.id+'&packid='+item.packid
			})
		},
		changeTabs(e){
			if(e.tab === '购买营销卡'){
				this.getAllList()
			}else if(e.tab === '我的营销卡'){
				this.getList()
			}
		},

		/**我的营销卡列表*/
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserFeePackageList({
				page:this.page,
				pagesize:10,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pagecount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		},/**购买营销卡*/
		getAllList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserFeePackageBuyList({
				page:this.page,
				pagesize:10,
			}).then(res=>{
				this.pageCount = res.pagecount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		},
		onActive(){
			ActivateUserFeePackage({
				couponnum: this.cardId,
				couponpassword: this.cardPwd,
			 			
			 }).then(res=> {
			   if (res.result) {
			     uni.showToast({
			     	title: '激活成功',
			     	
			     })
			   } else {
		
			     uni.showToast({
			     	title: res.msg,
			     	icon:'none'
			     })
			   }
			 });       
		}
	}
}
